import {
  isSet,
  normalizeResource,
} from './utils'
import { Promise } from 'es6-promise'

const ResourcesMutator = ({getResources, props, base}) => {

  base = base || ''

  return new Promise((resolve, reject) => {
    let mutator = {}
    let resources = getResources()

    for (let k in resources) {

      const resource = normalizeResource(resources[k])

      mutator[k] = (id) => {

        let resource = normalizeResource(getResources({id, props})[k])

        let subMutator = {

          update(payload, callback) {
            resolve({
              uri: `${base}${resource.base}${resource.update.uri}`,
              operationName: 'update',
              resource,
              resourceName: k,
              method: resource.update.method,
              payload,
              callback,
            })
          },

          read() {
            resolve({
              uri: `${base}${resource.base}${resource.read.uri}`,
              operationName: 'read',
              resource,
              resourceName: k,
              method: resource.read.method,
              callback,
            })
          },

          remove(callback) {
            resolve({
              uri: `${base}${resource.base}${resource.remove.uri}`,
              operationName: 'remove',
              resource,
              resourceName: k,
              method: resource.remove.method,
              callback,
            })
          },
        }

        for (let sk in resource.subs) {
          subMutator[sk] = ResourcesMutator({
            getResources: resource.subs,
            props,
            base: `${base}${resource.base}${resource.select}`,
          }).then(resolve)
        }

        return subMutator

      }

      mutator[k].create = (payload, callback) => {
        resolve({
          uri: `${base}${resource.base}${resource.create.uri}`,
          operationName: 'create',
          resource,
          resourceName: k,
          method: resource.create.method,
          payload,
          callback,
        })
      }

      mutator[k].list = () => {
        resolve({
          uri: `${base}${resource.base}${resource.list.uri}`,
          operationName: 'list',
          resource,
          resourceName: k,
          method: resource.list.method,
        })
      }

    }
  })
}

export default ResourcesMutator



